.transactionHistory {
  width: 600px;
  margin-bottom: 20px;
  border: 1px solid #dddddd;
  border-collapse: collapse;
}
.transactionHistory th {
  font-weight: bold;
  padding: 5px;
  background: #efefef;
  border: 1px solid #dddddd;
}
.transactionHistory td {
  border: 1px solid #dddddd;
  padding: 5px;
}
